:root {
  --Iclr: rgb(123, 117, 117);
  --IIclr: #fff;
  --IIIclr: #000;
  --IVclr: rgb(223, 223, 223);
  --Vclr: grey;
  --VIclr: rgb(57,183,255);
  --trans1: all .2s ease;
  --trans2: all .4s ease;
  --trans3: all .6s ease;
}

h1, h2, h3, h4, h5, p, span, a {
  overflow: hidden;
}

/* custom scroll bar */
::-webkit-scrollbar {
  width: 15px;
  background-color: transparent;
}

/* Track */
::-webkit-scrollbar-track {
  margin: 5px;
  background: #fff;
  border-radius: 20px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background: #77bee7;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(57,183,255);
  background: var(--VIclr);
}

/* for top linking box */
.toTop {
  background-color: #fff;
  width: 45px;
  height: 35px;
  position: fixed;
  bottom: 70px;
  right: 40px;
  border-radius: 5px;
  box-shadow: 0 0 10px grey, inset 0 0 2px black;
  cursor: pointer;
  text-decoration: none;
  -webkit-transform: translateX(100px);
          transform: translateX(100px);
  transition: all .2s ease;
  transition: var(--trans1);
  z-index: 99;
}

.toTop i {
  font-size: 20px;
  color: rgb(57,183,255);
  color: var(--VIclr);
}

.toTop:hover {
  -webkit-transform: scale(1.2) !important;
          transform: scale(1.2) !important;
}

