/* contact */
.Contact {
  position: relative;
  top: 0;
  z-index: 9999;
  background: url(/NTB/static/media/back.48108c78.jpeg);
  margin-bottom: 4rem;
}

.contact {
  width: 100;
  max-height: 80vh;
  justify-content: space-around !important;
}

.Cleft, .Cright {
  width: 45%;
  padding: 8rem 0 !important;
}

.Cright p {
  font-size: 2rem;
}

.Cleft input,
.Cleft textarea {
  width: 100%;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid rgb(57,183,255);
  border-bottom: 1px solid var(--VIclr);
  font-size: 2rem;
  outline: none;
  margin-top: 1rem;
  transition: all .2s ease;
  transition: var(--trans1);
}

.Cleft textarea:hover,
.Cleft textarea:focus,
.Cleft input:hover,
.Cleft input:focus {
  background-color: transparent;
  border: none;
  border-bottom: 5px solid rgb(57,183,255);
  border-bottom: 5px solid var(--VIclr);
  font-size: 2rem;
  outline: none;
  box-shadow: none;
}

.contactMap iframe {
  width: 100%;
  height: 50vh;
  border-radius: 2rem;
  margin: 2rem 0;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
  transition: all .2s ease;
  transition: var(--trans1);
}

.contactMap iframe:hover {
  -webkit-transform: scale(1);
          transform: scale(1);
}

/* for footer */
.footer {
  width: 100%;
  min-height: 40vh;
  position: relative;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.4)), url(/NTB/static/media/back.48108c78.jpeg);
  justify-content: space-around;
  margin-top: 2rem;
  padding-top: 1rem;
}

.Fleft {
  width: 40%;
  height: 70%;
  margin-bottom: 7%;
}

.Fleft .content {
  padding-left: 2rem;
  padding-top: 2rem;
}

.Fleft .content h3 {
  font-size: 3.5rem;
  font-weight: 600;
  color: rgb(57,183,255);
  color: var(--VIclr);
}

.Fleft .content p {
  font-size: 1.8rem;
  color: rgb(39, 38, 38);
}

.Fright {
  width: 39%;
  height: 70%;
  margin-bottom: 7%;
  flex-direction: column;
}

.Fright li {
  font-size: 2rem;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  color: rgb(39, 38, 38);
}

.Fright li i {
  padding-right: 1rem;
  color: #000;
}

.Fright li:first-child {
  font-size: 3rem;
  font-weight: 600;
  color: rgb(57,183,255);
  color: var(--VIclr);
  -webkit-transform: translateX(-10px);
          transform: translateX(-10px);
}

.footerBtm {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px;
  background-color: rgb(57,183,255);
  background-color: var(--VIclr);
  justify-content: space-around;
}

.footerBtm p {
  color: #fff;
  font-size: 1.6rem;
}

.footerBtm a {
  font-size: 2rem;
  font-weight: 600;
  color: rgb(223, 223, 223);
  color: var(--IVclr);
  text-decoration: none;
}

.footerBtm a:hover {
  color: #fff;
