  .home .left1 {
    width: 100%;
  }
  .home .content {
    width: 100%;
    flex-direction: column;
  }
  .home .content .card {
    width: 100%;
    margin: 1rem;
  }
  .home img {
    width: 60%;
  }
  .homeimg{
      display: none;
  }
  .card p {
    font-size: 2rem;
  }
  .form form {
    width: 90%;
