  }
  .booksNav .input-group{
    width: 30vw;
  }
  /* footer */
  .toTop{
    right: 80px;
  }
  .footer{
    flex-direction: column;
    /* min-height:60vh ; */
    
  }
  .Fright{
    margin-bottom: 80px;
  }
  .Fright,.Fleft{
    width: 100%;
  }
}
